<template>
  <div>

    <div class="row mx-0">

      <div class="col-md-6">

        <div class="form-group">
          <label>عنوان ویدئو</label>
          <input type="text" class="form-control"/>
        </div>

        <div class="form-group">
          <label>توضیحات</label>
          <textarea class="form-control"></textarea>
        </div>

        <div class="form-group">
          <label>بسته تبلیغاتی</label>
          <select class="form-control">
            <option selected>-- انتخاب بسته --</option>
            <option>1000 نمایش یک ماه</option>
            <option>5000 نمایش سه ماه</option>
            <option>12000 نمایش شش ماه</option>
            <option>30000 نمایش 12 ماه</option>
          </select>
        </div>


        <div class="form-group">
          <label>مدت زمان</label>
          <div class="form-row">
            <div class="form-group col">
              <label>ثانیه</label>
              <input type="number" min="0" max="60" class="form-control"/>
            </div>
            <div class="form-group col">
              <label>دقیقه</label>
              <input type="number" min="0" max="60" class="form-control"/>
            </div>
          </div>
        </div>

      </div>

      <div class="col-md-6">

        <div class="form-group">
          <label>اسکرینشات ویدئو</label>
          <input type="file" ref="imageInputElement" v-on:change="onImageChange" accept="image/jpeg,image/png,image/gif"
                 class="d-none"/>

          <div class="aspect-100" id="item-image">
            <img src="../../../assets/images/store/default-item.jpg" ref="imageElement"/>
          </div>

          <div class="d-flex align-items-center justify-content-center text-center mt-3">

            <span class="cursor-pointer" v-if="!cropper" @click="selectImage">
              <span class="fa fa-folder-open fa-2x text-warning"></span>
            </span>

            <span class="cursor-pointer ml-2" v-if="cropper" @click="cropperConfirm">
              <span class="fa fa-cloud-upload-alt fa-2x text-info"></span>
            </span>

          </div>
        </div>

      </div>

    </div>

    <div class="mt-3 text-center">
      <button class="btn btn-outline-success">
        ثبت اطلاعات
      </button>
    </div>

  </div>
</template>

<script>
import Cropper from 'cropperjs';
import 'cropperjs/dist/cropper.min.css';

export default {
  name: "AdvertisesUploadVideo",
  data: () => ({
    cropper: null
  }),
  methods: {
    selectImage: function () {
      this.$refs.imageInputElement.click();
    },
    onImageChange: function () {
      if (this.$refs.imageInputElement.files.length > 0) {
        const fr = new FileReader();
        fr.onload = () => {
          this.$refs.imageElement.src = fr.result;
          this.cropper = new Cropper(this.$refs.imageElement, {
            aspectRatio: 1 / 1
          });
        }
        fr.readAsDataURL(this.$refs.imageInputElement.files[0]);
      }
    },
    cropperConfirm: function () {
      if (this.cropper) {
        const src = this.cropper.getCroppedCanvas().toDataURL();
        this.cropper.destroy();
        this.cropper = null;
        this.$refs.imageElement.src = src;
      }
    }
  }
}
</script>

<style scoped>
#item-image {
  background: var(--light);
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.6);
}
</style>